
































































































































































































































import { MessageBoxOptions } from "electron";
import { inspect } from "util";
import Vue from "vue";

export default Vue.extend({
  name: "create-user",
  data: () => {
    return {
      step: 1,
      month: "",
      batch: "",
      batchMessage: "",
      members: [] as members[],
      memberModel: null as createUserFields | null,
      noOfChits: null as string | null,
      users: [] as createUserFields[],
      disableButtons: false as boolean,
      disableInputs: false as boolean,
      loading: false as boolean,
      submited: false as boolean,
      success: false as boolean,
      skipValidation: false as boolean,
      window: window,
    };
  },
  computed: {
    totalChits() {
      let total = 0;
      this.members.forEach((member) => {
        total += member.noOfChits;
      });
      return total;
    },
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Create Group";
        case 2:
          return "Batch Name";
        case 3:
          return "Add members for " + this.batch + " batch";
        case 4:
          return "Final";
      }
    },
  },
  watch: {
    step: function () {
      window.resizeWindowToCard();
      if (this.step === 3) this.getUsers();
    },
  },
  methods: {
    removeMember(member: members) {
      this.users.push(member.info);
      this.members.splice(this.members.indexOf(member), 1);
    },
    addMember() {
      if (this.memberModel && this.noOfChits) {
        let removed = this.users.splice(this.users.indexOf(this.memberModel), 1);
        if(removed.length !== 1){
          this.users.push(...removed);
          return;
        }
        this.members.push({
          info: removed[0],
          noOfChits: parseFloat(this.noOfChits),
        });;
        this.memberModel = null;
        this.noOfChits = null;
      }
    },
    next() {
      document.getElementById("next")!.click();
    },
    batchChange() {
      if (!this.batch) return;
      let bch = this.batch;
      bch = bch.toUpperCase().replace(/[^A-Z]/g, "");
      bch = bch.charAt(bch.length - 1);
      setTimeout(() => {
        this.batch = bch;
      }, 0);
    },
    stepForward() {
      if (this.skipValidation) {
        this.step++;
        return;
      }
      this.disableButtons = true;
      this.disableInputs = true;
      this.loading = true;
      this.validate((success) => {
        if (success) this.step++;
        this.disableInputs = false;
        this.disableButtons = false;
        this.loading = false;
      });
    },
    getUsers() {
      this.loading = true;
      this.disableInputs = true;
      window.ipcrenderer.once(
        "get-users-data",
        (event, data: createUserFields[]) => {
          this.users = data;
          console.log(data);
          this.disableInputs = false;
          this.loading = false;
        }
      );
      window.ipcrenderer.send("get-users-data");
    },
    reloadUsers(){
      this.loading = true;
      this.disableInputs = true;
      window.ipcrenderer.once(
        "get-users-data",
        (event, data: createUserFields[]) => {
          console.log(data);
          this.members.forEach(member=>{
            this.users.splice(this.users.indexOf(member.info), 1);
          });
          this.disableInputs = false;
          this.loading = false;
        }
      );
      window.ipcrenderer.send("get-users-data");
    },
    validate(fn: (success: boolean) => void) {
      if (this.step > 3) return fn(true);
      switch (this.step) {
        case 1:
          if (!this.month) return fn(false);
          return fn(true);
          break;
        case 2:
          if (!this.batch) return fn(false);
          this.batchMessage = "";
          window.ipcrenderer.once(
            "batch-exists",
            (event, err: sqliteError, response: boolean) => {
              if (!response) {
                fn(true);
              } else {
                this.batchMessage = "Batch Already exists";
                fn(false);
              }
            }
          );
          window.ipcrenderer.send("batch-exists", this.batch, this.month);
          break;
        case 3:
          if (!this.members) return fn(false);
          if (this.totalChits !== 20) return fn(false);
          return fn(true);
          break;
      }
    },
    submit() {
      if (this.submited) {
        window.close();
        return;
      }
      this.submited = true;
      this.disableInputs = true;
      this.skipValidation = true;
      window.ipcrenderer.once(
        "create-group",
        (event, err: sqliteError, data: createGroupFields) => {
          if (err) {
            window.ipcrenderer.send("show-message-box", {
              message: "Some error occoured during the creation of Group",
              type: "error",
              title: "Cannot create Group!",
              detail: inspect(err),
            } as MessageBoxOptions);
            this.success = false;
          } else if (data) {
            window.ipcrenderer.send("show-message-box", {
              message: "Group created SUCCESSFULLY !",
              type: "info",
              title: "Created New Group!",
              detail: inspect(data),
            } as MessageBoxOptions);
            this.success = true;
          } else {
            window.ipcrenderer.send("show-message-box", {
              message:
                "Some error occoured during the creation of Group\nTry checking batch name.",
              type: "error",
              title: "Cannot create Group!",
              detail: "err and detail variables are undefined or not truthly",
            } as MessageBoxOptions);
            this.success = false;
          }
        }
      );
      window.ipcrenderer.send("create-group", {
        month: this.month,
        batch: this.batch,
        members: this.members,
      });
    },
  },
  components: {},
  mounted() {
    window.resizeTo(550, 700);
    window.document.title = "Create Group";
  },
});

interface members {
  info: createUserFields;
  noOfChits: number;
}
